.section-container{
    background-color: #FFF29C;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.main-bg{
    margin-top: -50px;
}

.page-heading{
    font-weight: 900;
    padding-top: 5px; 
    font-size: 5rem;
    color: #724319;
    z-index: 2;
}

.page-text{
    font-size: 2rem;
    font-weight: 900;
    color: #B99B5B;
    z-index: 1;
}

.page-description{
    font-weight: 300;
    color: #909090;
    max-width: 70%;
    font-size: large;
    z-index: 1;
    opacity: 85%;
    text-align: justify;
}

.dog-bone{
    opacity: 65%;
    margin-top: -70px;
    float: right;
}

.decor-blob {
    position: absolute;
    right: 50px;
    width: 850px;
    height: 700px;
    background: #FFF29C;
    border-radius: 50% 40% 60% 30% / 50% 60% 40% 30%;
    z-index: 0;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
}

.blob-input{
    border-radius: 30px;
    opacity: 77%;
    height: 70px;
    font-size: 30px;
    max-width: 480px;
    text-align: center;
    margin-top: 70px;
    margin-left: 180px;
    background-color: #724319;
}

.signup-bg{
    margin-top:505px;
    position: relative;
    margin-left: 60px;
}

.submit-signup{
    text-decoration: underline;  
    background: none;
    border: none;
    cursor: pointer;
    max-width: 150px;
}

.submit-signup:hover{
    background-color: black;
}

.map-visit{
    left: 0;
}

.phone-connector{
    position: absolute;
    right: 0;
    width: 20%;
    margin-top: -215px;
}

.circle-bg{
    border-radius: 9999px;
    width: 300px;
    height: 300px;
    background-color: #ED8A0A;
    margin-right: 50px;
    margin-bottom: 10px;

}

.circle-bg img {
    transition: transform 0.3s ease; 
    object-fit: cover; 
}

.circle-container:hover{
    transform: scale(1.1); 
}

.circle-bg img:hover{
    transform: scale(1.1);
}


.pets-circle-container{
    margin-left: 40px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}


@media screen and (max-width: 800px){
    .main-bg{
        max-width: 80%;
    }
    .section-container{
        flex-direction: column;
    }
    .main-bg{
        margin-top: -30px;
    }

    .page-heading{
        font-size: 3rem;
        padding-left: -20px;
    }

    .page-heading{
        margin-top: -90px;
    }

    .about-section{
        flex-direction: column-reverse;
    }

    .signup-bg{
        margin-top:385px;
        margin-left: 40px;
    }

    .decor-blob{
        width: 420px;
        height: 400px;
        right: 40px;
    }

    .blob-input{
        height: 50px;
        margin-top: 45px;
        margin-left: 100px;
        max-width: 200px;
        font-size: 18px;
    }

    .map-container{
        flex-direction: column-reverse;
    }

    .phone-connector{
        width: 30%;
        margin-top: -100px;
    }

    .pets-circle-container{
        flex-direction: column;
    }
}